@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-dark:rgba(255, 255, 255, 0);
  --primary-purple: rgb(64, 0, 77);
}

body {
  font-family: "Roboto Condesed" sans-serif;
}
/*************************** NAV BAR ***************************/

.imageLogo {
  width: 3.125rem;
}

/*----- MENU LINKS -----*/

nav a.nav-link {
  font-size: .8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  color: #fff !important;
  color: var(--primary-white) !important;
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  border-top: 0.1875rem solid rgba(255, 255, 255, 0);
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 5rem;
}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus{
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-color{
  background-color: rgb(64, 0, 77) !important;
  background-color: var(--primary-purple) !important;
}
